import React, { Component } from 'react'
import ListItem from 'components/ListItem/ListItem';
import styles from './_TaskList.scss';

const listData = [
  {title: 'test1', important: true, complete: false, date: 1526227200000, comment: 'test comment'},
  {title: 'test2', important: true, complete: false, date: 1526227200000, comment: ''},
  {title: 'test3', important: false, complete: false, date: 1526227200000, comment: '', file: {name: 'file1'}},
  {title: 'test4', important: false, complete: true, date: 1526227200000, comment: ''}
]

export default class TaskList extends Component {
  render() {
    return (
      <div className={styles.TaskList}>
        { listData.map((item, key) => <ListItem item={item} key={key}/>) }
      </div>
    )
  }
}



