import React, { Component } from 'react'
import styles from './_TaskBuild.scss';

export default class TaskBuild extends Component {
  render() {
    return (
      <div className={styles.TaskBuild}>
        <i className="fas fa-plus" style={styles.icon}></i>
        <p className={styles.hint}>Add task</p>
      </div>
    )
  }
}



