import React, { Component } from 'react'
import PropTypes from 'prop-types';
import ListItemTitle from './ListItemTitle/ListItemTitle'
import ListItemSubtitle from './ListItemSubtitle/ListItemSubtitle'
import ListContent from './ListContent/ListContent'
import styles from './_ListItem.scss'

export default class ListItem extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isImportant: this.props.item.important ? styles.important : ''
    }
  }
  render() {
    return (
      <div className={`${styles.ListItem}`}>
        <div className={`${styles.header} ${this.state.isImportant}`}>
          <ListItemTitle 
            text={this.props.item.title} 
            important={this.props.item.important}
            editable={this.props.item.editable}
            complete={this.props.item.complete}/>
          <ListItemSubtitle 
            date={this.props.item.date}
            file={this.props.item.file}
            comment={this.props.item.comment}
          />
        </div>
        <ListContent>

        </ListContent>
      </div>
    )
  }
}

ListItem.propTypes  = {
  item: PropTypes.object
}

ListItem.defaultProps = {
  item: {
    title: '',
    editable: false,
    complete: false,
    important: false,
    date: 0,
    comment: '',
    file: {}
  }
}


