import React, { Component } from 'react'
import PropTypes from 'prop-types';
import styles from './_ListItemTitle.scss';

export default class ListItemTitle extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isComplete: this.props.complete ? styles.complete : '',
      isImportant: this.props.important ? styles.important : '',
      cbIcon: this.props.complete ? 'fa-check-square' : 'fa-square',
      starIcon: this.props.important ? 'fas' : 'far'
    }
  }
  render () {
    return (
      <div className={`${styles.ListItemTitle} ${this.state.isComplete} ${this.state.isImportant}`}>
        <div className={styles.checkbox}>
          <i className={`fas ${this.state.cbIcon } fa-lg`}></i>
        </div>
        {/* <input type='text' placeholder='Type Something Here...'/> */}
        <p className={styles.title}>{this.props.text}</p>
        <div className={styles.action}>
          <div className={styles.importantMask}>
            <i className={`${this.state.starIcon} fa-star fa-lg`} />
          </div>
          <div className={styles.edit}>
            <i className="fas fa-pencil-alt fa-lg"></i>
          </div>
        </div>
      </div>
    )
  }
}

ListItemTitle.propTypes = {
  text: PropTypes.string,
  editable: PropTypes.bool
}

ListItemTitle.defaultProps = {
  text: '',
  editable: false
}


