import React, { Component } from 'react'
import styles from './_ListItemSubtitle.scss'
import PropTypes from 'prop-types';
import * as moment from 'moment';

export default class ListItemSubtitle extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showCalender: this.props.date ? styles.show : '',
      showFile: Object.keys(this.props.file).length ? styles.show : '',
      showComment: this.props.comment ? styles.show : ''
    }
  }
  render() {
    return (
      <div className={styles.ListItemSubtitle}>
        <div className={`${styles.icon} ${this.state.showCalender}`}>
          <i className="far fa-calendar-alt"></i>
          <span className='text'>{moment(this.state.date).format('YYYY/MM/DD')}</span>
        </div>
        <div className={`${styles.icon} ${this.state.showFile}`}>
          <i className="far fa-file"></i>
        </div>
        <div className={`${styles.icon} ${this.state.showComment}`}>
          <i className="far fa-comment-dots"></i>
        </div>
      </div>
    )
  }
}

ListItemSubtitle.propTypes = {
  date: PropTypes.number,
  file: PropTypes.object,
  comment: PropTypes.string
}

ListItemSubtitle.defaultProps = {
  date: 0,
  file: {},
  comment: ''
}


